import React from "react";

import "./Card.css";
import PropTypes from "prop-types";

const Card = (props) => {
  return (
    <div
      className="card"
      style={{
        background: `linear-gradient(to left, ${props.fcolor}, ${props.scolor})`,
      }}
    >
      <img src={props.img} alt="lang.svg" />
      <h3>{props.lengua}</h3>
    </div>
  );
};

Card.propTypes = {
  lengua: PropTypes.string,
  img: PropTypes.string,
  fcolor: PropTypes.string,
  scolor: PropTypes.string,
};

export default Card;
